.Card {
    margin: 0.25rem;
    position: relative;
    width: calc(5.5rem * 1);
    height: calc(8.5rem * 1);
    /* box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 4px 2px; */
}

.Card[hoverable="true"]:hover {
    z-index: 1;
    -webkit-transform: scale(2);
        -ms-transform: scale(2);
            transform: scale(2);
    /* width: calc(5.5rem * 2);
    height: calc(8.5rem * 2); */
}

.Card .CardFG {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    background: url(/Cards/Card%20Front.png) center center no-repeat;
}

.Card .CardBG {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background: url(/Cards/Card%20Back.png) center center no-repeat;
}

.Card {
    -webkit-transition: all .1s cubic-bezier(0.075, 0.82, 0.165, 1);
    -o-transition: all .1s cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: all .1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.Card .CardFG,
.Card .CardBG {
    -webkit-transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
    -o-transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.Card[fOb="back"] {
    /* box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 6px 8px; */
}

.Card[fOb="back"] .CardFG {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.Card[fOb="back"] .CardBG {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}

.Card .CardFG,
.Card .CardBG,
.Card .CardFace {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background-repeat: no-repeat;
    background-size: cover;
    image-rendering: optimizeSpeed;
    image-rendering: -moz-crisp-edges;
    image-rendering: -o-crisp-edges;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: -o-pixelated;
    image-rendering: pixelated;
    image-rendering: optimize-contrast;
    -ms-interpolation-mode: nearest-neighbor;
}

.Card .CardName {
    left: 50%;
    bottom: 10%;
    position: absolute;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}

.Card .CardCorner {
    top: 0;
    left: 0;
    position: absolute;
}

.Card .CardCorner[posX="R"] {
    right: 0;
    left: initial;
}

.Card .CardCorner[posY="B"] {
    bottom: 0;
    top: initial;
}

.Card[fOb="back"] .CardFace,
.Card[fOb="back"] .CardCorner {
    display: none;
}


